body {
  background: #f5f5f5;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
    overflow: hidden;
	padding-top: 10px;
    color: #4d4d4d;
    font: 100px 'Helvetica Neue', Helvetica, Arial, sans-serif;
    line-height: 1.4em;
    width: 100%;
    box-shadow: inset 0 -2px 1px rgba(0,0,0,0.03);
}

.header__headline {
    color: rgba(175, 47, 47, 0.15);
	position: absolute;
    top:-200px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    font: 100px 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

.header__switch-complited {
    margin-left: 7px;
	width: 35px;
	height: 35px;
    float: left;
    background: url("/img/1.png");
    background-size: cover;
    border: none;
    outline: none;
    -webkit-transition: all ease-out .2s;
     transition: all ease-out .2s; 
}

.header__switch-active {
    margin-left: 7px;
	width: 35px;
	height: 35px;
    float: left;
    background: url("/img/1.png");
    background-size: cover; /* Для Firefox */ /* Для IE */
    -webkit-transform: rotate(180deg); /* Для Safari, Chrome, iOS */ /* Для Opera */
    transform: rotate(180deg);
    border: none;
    outline: none;
    -webkit-transition: all ease-out .2s;
     transition: all ease-out .2s; 
}


.header__input {
    background: #fff;
	width: 80%;
	display:block;
	position: relative;
	margin-top: -16px;
    padding: 16px 16px 16px 8px;
    border: none;
    float: left;
    background: rgba(0, 0, 0, 0.003);
    margin-left: 10px;
    font-size: 24px;
    font-family: inherit;
    font-weight: inherit;
    line-height: 1.4em;
    border: 0;
    outline: none;
    color: inherit;
}

.header__input::-webkit-input-placeholder {
    color:#d9d9d9;
	font-style: italic;
}

.header__input::-ms-input-placeholder {
    color:#d9d9d9;
	font-style: italic;
}

.header__input::placeholder {
    color:#d9d9d9;
	font-style: italic;
}


@media screen and (max-width: 450px) {
    .header__input::-webkit-input-placeholder {
        font-size: 18px;
    }
    .header__input::-ms-input-placeholder {
        font-size: 18px;
    }
    .header__input::placeholder {
        font-size: 18px;
    }


    .header__input {
        width: 70%; 
    }
}

.case {
    margin: 0;
    padding: 0;
}

.case__item-active {
    color: #4d4d4d;
	font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
	font-size: 24px;
	align-items: center;
	display:flex;
	justify-content: space-between;
	width: 100%;
	border-bottom: 1px solid #ededed;
	text-decoration: line-through;
	overflow: auto;
	-webkit-transition: all ease-out .2s;
    transition: all ease-out .2s;
}

.case__item-complited {
	display:flex;
	color: #7a8183;
	font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
	font-size: 24px;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	border-bottom: 1px solid #ededed;
	overflow: auto;
	
}

.case__item_img-active {
	background: url("/img/ready.png");
	background-size: cover;
    position: relative;
	align-items: center;
	display: block;
	justify-content: center;
	border: 2px solid #32950C;
	border-radius: 50%;
	width: 40px;
	height: 40px;
	padding-left: 0;
	margin-left: 2%;
	outline: none;
}

.case__item_img-complited {
	background: #ffffff;
    position: relative;
	align-items: center;
	display: block;
	justify-content: center;
	border: 2px solid #32950C;
	border-radius: 50%;
	width: 40px;
	height: 40px;
	outline: none;
	padding-left: 0;
	margin-left: 2%;
}

.case__item_list {
	display:inline-block ;
	padding-top: 9px;
	padding-bottom: 10px;
	width: 80%;
	background: white;
	word-wrap: break-word;
}

.case__item_remove {
    display: block;
	width: 35px;
	height: 35px;
	padding-right: 1%;
}

.case__item-active:hover .case__item_remove {
    background: url("/img/del.png");
    background-size: cover;
	width: 35px;
	height: 35px;
	float: right;
	opacity: .8;
}

.case__item-complited:hover .case__item_remove {
    background: url("/img/del.png");
    background-size: cover;
	width: 35px;
	height: 35px;
	float: right;
	opacity: .8;
}

.case__item-complited:active .case__item_remove {
    background: url("/img/del.png");
    background-size: cover;
	width: 35px;
	height: 35px;
	float: right;
	opacity: .8;
}

.case__item_remove:hover:after {
    content: '';
	background: url("/img/del-hover.png");
    background-size: cover;
	width: 35px;
	height: 35px;
	float: right;
}

@media screen and (max-width: 550px) {
	.case__item_list {
		padding-top: 8px;
		padding-bottom: 8px;
		width: 70%;
		font-size: 18px;
	}
	
}

@media screen and (max-width: 450px) {
	.case__item_list {
		width: 65%;
		font-size: 16px;
	}	
}
.footer {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-around;
    position: relative;
    height: 50px;
    overflow: hidden;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2),
    0 8px 0 -3px #f6f6f6, 0 9px 1px -3px rgba(0, 0, 0, 0.2),
    0 16px 0 -6px #f6f6f6, 0 17px 2px -6px rgba(0, 0, 0, 0.2); 
}



.footer__span {
    font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
	line-height: 1.4em;
	color: #4d4d4d;
	border: 2px solid #4d4d4d;
	white-space: nowrap;
	border-radius: 15%;
	padding: .5%;
	margin-left: 3%;
}

.footer__swich {
	display: flex;
	align-items: center;
	justify-content: space-between;
	color: #4d4d4d;
	font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
	overflow: hidden;
	width: 35%;
}
.footer__swich_list {
    position: relative;
	border-radius: 3px;
	border: 2px solid #fff;
	display: inline-block;
	padding: 3px 5px;
	border: 2px solid #4d4d4d;
}

.footer__swich_list-selected{
	position: relative;
	padding: 3px 5px;
	border: 2px solid #4d4d4d;
	border-radius: 3px;
	font-weight: bold;
	background: green;
	color: #fff;
}

.footer__btn {
	display: flex;
    outline: 0;
	position: relative;
	box-sizing: border-box;
	align-items: center;
	font: 12px 'Helvetica Neue', Helvetica, Arial, sans-serif;
	color: #ffffff;
	padding: 3px 5px;
	border: 2px solid #4d4d4d;
	border-radius: 3px;
	background: rgb(202, 81, 81);
	width: 100px;
	font-weight: 100;
	margin-right: 3%;
	white-space: nowrap;
}

@media screen and (max-width: 550px) {
	.footer__swich {
		font-size: 12px;
		padding: 0;
	}
}

@media screen and (max-width: 470px) {
	.footer__swich {
		width: 40%;
	}
}

@media screen and (max-width: 420px) {
	.footer__swich {
		width: 47%;
		font-size: 11px;
	}

	
.footer__span {
    font-size: 11px;
	}
}
.page {
  -webkit-font-smoothing: antialiased;
	cursor: default;
	display: block;
	max-width: 550px;
	width: 90%;
	background: #fff;
	margin-top: 30vh;
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	position: relative;
	color: #333333;
	box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
}



